.background {
  width: 70vw;
  height: 70vh;
  /* background-color: #f9d7d5; */
  background-image: linear-gradient(to bottom right, #f9d7d5, #a1cad0);
  border-radius: 20px;
}

.login {
  text-align: center;
  font-family: "Mali";
  /* background-color: aquamarine; */
  font-size: 5vh;
}

.inputfield {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* background-color: black; */
}

.LabeledInput {
  /* background-color: aqua; */
  width: 50%;
}

.buttonfield {
  display: flex;
  width: 50%;
  /* background: black; */
  justify-content: center;
}

.button {
  font-family: "Mali";
  font-size: 2.4vh;
  /* background-color: #d7ddc1; */
  background-image: linear-gradient(to bottom right, #fbece5, #d7ddc1);
  box-sizing: border-box;
  width: 40%;
  height: 6vh;
  margin: 8%;
  border-radius: 10px;
}
